import { Notification } from "@/utils/Notification.js";
import { Notify } from "@/utils/Notify.js";

export default {
  data() {
    return {
      dataIndex: 0,
      mapBtnArr: [
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/transport.png",
          activeUrl: "data:image/png;base64,",
          text: "公交",
          type: "transit"
        },
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/subway.png",
          activeUrl: "data:image/png;base64,",
          text: "地铁",
          type: "subway"
        },
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/school.png",
          activeUrl: "data:image/png;base64,",
          text: "学校",
          type: "school"
        },
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/hospital.png",
          activeUrl: "data:image/png;base64,",
          text: "医院",
          type: "hospital"
        },
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/bank.png",
          activeUrl: "data:image/png;base64,",
          text: "银行",
          type: "bank"
        },
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/leisure.png",
          activeUrl: "data:image/png;base64,",
          text: "休闲娱乐",
          type: "play"
        },
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/shopping.png",
          activeUrl: "data:image/png;base64,",
          text: "购物",
          type: "shop"
        },
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/food.png",
          activeUrl: "data:image/png;base64,",
          text: "餐饮",
          type: "eat"
        },
        {
          defaultUrl: "http://cdn.haofang.net/static/uuminiapp/map/sports.png",
          activeUrl: "data:image/png;base64,",
          text: "运动健身",
          type: "sport"
        }
      ]
    };
  },

  props: {
    currentIndex: {
      type: Number,
      default: 0
    }
  },

  watch: {
    currentIndex(newValue, oldValue) {
      this.dataIndex = newValue;
    }
  },

  mounted() {
    this.dataIndex = this.currentIndex;
  },

  methods: {
    maptBtn(item, index) {
      // this.UserBehavior.lookTypeMap(item.text);
      this.$emit("NearInfoMapBtnChanged", {
        item: item,
        index: index
      });
      if (this.dataIndex < 0) return;

      this.dataIndex = index;
    }
  }
};
